import { BrowserModule } from '@angular/platform-browser';
import { ErrorHandler, NgModule } from '@angular/core';
import { IonicApp, IonicErrorHandler, IonicModule } from 'ionic-angular';
import { SplashScreen } from '@ionic-native/splash-screen';
import { StatusBar } from '@ionic-native/status-bar';
import { JsonpModule } from "@angular/http";
import { Keyboard } from "@ionic-native/keyboard";
import { HttpModule } from "@angular/http";

//页面类
import { MyApp } from './app.component';
import { HomePage } from '../pages/home/home';
import { ProjectCreatePage } from "../pages/project-create/project-create";
import { MilestoneDetailPage } from "../pages/milestone-detail/milestone-detail";
import { ProjectDetailPage, PopoverPage } from "../pages/project-detail/project-detail";
import { SubtaskPage } from "../pages/subtask/subtask";
import { SearchPage } from "../pages/search/search";
import { ContactPage } from "../pages/contact/contact";
import { ForgetPage } from "../pages/forget/forget";
import { NewpwPage } from "../pages/newpw/newpw";

//组件类
import { ComponentsModule } from "../components/components.module";
import { CalendarModule }  from "ion2-calendar";
import { MultiPickerModule } from 'ion-multi-picker';

//pipe类
import { PipesModule } from "../pipes/pipes.module";
import { AppService } from "./app.service";
import {LoginPage} from "../pages/login/login";
import { KeycloakServiceProvider } from '../providers/keycloak-service/keycloak-service';
import { Keycloak2Provider } from '../providers/keycloak2/keycloak2';

//第三方库

@NgModule({
  declarations: [
    MyApp,
    HomePage,
    ProjectCreatePage,
    MilestoneDetailPage,
    ProjectDetailPage,
    SubtaskPage,
    SearchPage,
    ContactPage,
    LoginPage,
    ForgetPage,
    NewpwPage,

    PopoverPage,
  ],
  imports: [
    BrowserModule,
    ComponentsModule,
    PipesModule,
    HttpModule,
    IonicModule.forRoot(MyApp, {
      backButtonText: '',
      backButtonIcon: 'md-arrow-back',
      //mode: 'ios',
      tabsHideOnSubPages: true,
      modalEnter: 'modal-slide-in',
      modalLeave: 'model-slide-out',
      tabsPlacement: 'bottom', //tabweizhi
      pageTransition: 'ios-transition' //使用ios页面动画
    }, 
{
  links: [
    { loadChildren: '../pages/contact/contact.module#ContactPageModule', name: 'ContactPage', segment: 'contact', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/newpw/newpw.module#NewpwPageModule', name: 'NewpwPage', segment: 'newpw', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/forget/forget.module#ForgetPageModule', name: 'ForgetPage', segment: 'forget', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/login/login.module#LoginPageModule', name: 'LoginPage', segment: 'login', priority: 'low', defaultHistory: [] }
  ]
}),
    JsonpModule,
    CalendarModule,
    MultiPickerModule,
  ],
  bootstrap: [IonicApp],
  entryComponents: [
    MyApp,
    HomePage,
    ProjectCreatePage,
    MilestoneDetailPage,
    ProjectDetailPage,
    SubtaskPage,
    SearchPage,
    ContactPage,
    LoginPage,
    ForgetPage,
    NewpwPage,

    PopoverPage,
  ],
  providers: [
    StatusBar,
    SplashScreen,
    Keyboard,
    AppService,
    {provide: ErrorHandler, useClass: IonicErrorHandler},
    KeycloakServiceProvider,
    Keycloak2Provider
  ]
})
export class AppModule {}



